<template>
    <div>
        <van-nav-bar
                title="政协届别委员联络站"
                right-text="退出"
                @click-right="onClickRight"
                fixed
                border
        >
        </van-nav-bar>
        <div style="margin-top: 56px;">


            <van-tabs v-model="activeName" @click="onClick">
                <van-tab name="0">
                    <div slot="title">
                        <van-row>
                            <van-col span="8"></van-col>
                            <van-col span="8">全部</van-col>
                            <van-col span="8">
                                <div style="padding: 10px 5px;" v-show="isShowAll">
                                    <van-tag round type="danger">{{replyInfo.allReply}}</van-tag>
                                </div>
                            </van-col>
                        </van-row>

                    </div>
                </van-tab>
                <van-tab name="1">
                    <div slot="title">
                        <van-row>
                            <van-col span="8"></van-col>
                            <van-col span="8">已解答</van-col>
                            <van-col span="8">
                                <div style="padding: 10px 5px;" v-show="isShowRe">
                                    <van-tag round type="danger">{{replyInfo.reReply}}</van-tag>
                                </div>
                            </van-col>
                        </van-row>

                    </div>
                </van-tab>
                <van-tab name="2">
                    <div slot="title">
                        <van-row>
                            <van-col span="8"></van-col>
                            <van-col span="8">未解答</van-col>
                            <van-col span="8">
                                <div style="padding: 10px 5px;" v-show="isShowNot">
                                    <van-tag round type="danger">{{replyInfo.notReply}}</van-tag>
                                </div>
                            </van-col>
                        </van-row>

                    </div>
                </van-tab>
            </van-tabs>
            <!--            <van-tabbar v-model="active">-->
            <!--                <van-tabbar-item  dot>标签</van-tabbar-item>-->
            <!--                <van-tabbar-item  info="5">标签</van-tabbar-item>-->
            <!--                <van-tabbar-item  info="20">标签</van-tabbar-item>-->
            <!--            </van-tabbar>-->
            <!--                        <div style="position: relative;z-index: 1;top:-35px;text-align: right;">-->
            <!--                            <van-row style="height: 0;">-->
            <!--                                <van-col span="8">-->
            <!--                                    <div style="width: 90%;">-->
            <!--                                        <van-tag round type="danger">10</van-tag>-->
            <!--                                    </div>-->
            <!--                                </van-col>-->
            <!--                                <van-col span="8">-->
            <!--                                    <div style="width: 90%;">-->
            <!--                                        <van-tag round type="danger">10</van-tag>-->
            <!--                                    </div>-->
            <!--                                </van-col>-->
            <!--                                <van-col span="8">-->
            <!--                                    <div style="width: 90%;">-->
            <!--                                        <van-tag round type="danger">10</van-tag>-->
            <!--                                    </div>-->
            <!--                                </van-col>-->
            <!--                            </van-row>-->
            <!--                        </div>-->
            <div style="text-align: left;">


                <van-list
                        v-model="loading"
                        :finished="finished"
                        finished-text="没有更多了"
                        @load="onLoad"
                >
                    <van-cell-group border v-for="item in getQuestionList" v-bind:key="item.id">
                        <van-cell

                                :title="item.question"
                                is-link
                                :label="item.questionTimeF"
                                @click="onClickItem(item)"
                                border
                        >

                        </van-cell>
                    </van-cell-group>

                </van-list>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "VipHome",
        data() {
            return {
                activeName: '0',
                list: [],
                loading: true,
                finished: false,
                pageIndex: 1,
                pageSize: 20,
                isShowAll: false,
                isShowRe: false,
                isShowNot: false,
                replyInfo: {
                    allReply: 0,
                    reReply: 0,
                    notReply: 0
                },
                currentName: null
            };
        },
        created() {
            this.getQuestionCount();
            this.getList(this.activeName);

        },
        methods: {
            onClickRight() {
                this.$toast('退出');
                this.$router.push({path: '/vipindex'});
            },
            onLoad() {
                this.pageIndex = this.$store.getters.questionList.length + 1;
                this.loading = true;
                this.addList(this.currentName)
            },
            onClickItem(item) {
                this.$store.dispatch('selectQuestionInfo', item);
                this.$router.push({path: '/advisory/' + item.id})

            },
            onClick(name,) {
                this.currentName = name;
                // eslint-disable-next-line no-console
                // console.log(name, title);
                this.getList(name);
                // this.$toast(title);
            },
            getQuestionCount() {
                /*http://58.54.251.155:8088/wzzgh-fwdt/committeeImp/getCommitteeQuestionCount?committeeId=*/
                const that = this;
                const id = that.$store.getters.curId;
                this.axios.get(`/getCommitteeQuestionCount?committeeId=${id}`, {})
                    .then(function (response) {
                        // eslint-disable-next-line no-console
                        console.log('QQQQQQQQQQQQQQQQ', response);
                        // that.memberList = response.data.data;
                        // eslint-disable-next-line no-console
                        console.log(response.data.data);
                        that.replyInfo = response.data.data;
                        // that.isShowAll = that.replyInfo.allReply > 0;
                        that.isShowRe = that.replyInfo.reReply > 0;
                        that.isShowNot = that.replyInfo.notReply > 0;
                    })
                    .catch(function (error) {
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });
            },
            getList(status) {
                /*http://58.54.251.155:8088/wzzgh-fwdt/committeeImp/getCommitteeQuestionList?
          committeeId=308e1f73-3b9d-424d-9aa9-ca35350f5208&status=1&pageIndex=1&pageSize=20*/
                const that = this;
                const id = that.$store.getters.curId;
                // eslint-disable-next-line no-console
                console.log(this, this.$axios, this.axios);
                this.axios.get(`/getCommitteeQuestionList?committeeId=${id}&status=${status}&pageIndex=${that.pageIndex}&pageSize=${that.pageSize}`, {})
                    .then(function (response) {
                        // eslint-disable-next-line no-console
                        console.log(response);
                        // that.memberList = response.data.data;
                        // eslint-disable-next-line no-console
                        console.log(response.data.data.content);
                        // that.list=response.data.data.content;
                        const list = this.$store.getters.questionList;
                        that.$store.dispatch('questionList', list.push(...response.data.data.content));
                        that.loading = false;
                        if (response.data.data.content.length < that.pageSize) {
                            that.finished = true;
                        }

                    })
                    .catch(function (error) {
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });
            },
            addList(status) {
                /*http://58.54.251.155:8088/wzzgh-fwdt/committeeImp/getCommitteeQuestionList?
          committeeId=308e1f73-3b9d-424d-9aa9-ca35350f5208&status=1&pageIndex=1&pageSize=20*/
                const that = this;
                const id = that.$store.getters.curId;
                // eslint-disable-next-line no-console
                console.log(this, this.$axios, this.axios);
                this.axios.get(`/getCommitteeQuestionList?committeeId=${id}&status=${status}&pageIndex=${that.pageIndex}&pageSize=${that.pageSize}`, {})
                    .then(function (response) {

                        that.$store.dispatch('questionList', response.data.data.content);
                        that.loading = false;
                        if (response.data.data.content.length < that.pageSize) {
                            that.finished = true;
                        }

                    })
                    .catch(function (error) {
                        // eslint-disable-next-line no-console
                        console.log(error);
                    });
            }
        },
        computed: {
            getQuestionList() {
                // eslint-disable-next-line no-console
                console.log(this.$store.getters.questionList);
                return this.$store.getters.questionList;
            }
        }
    }

</script>

<style scoped>
    .van-nav-bar {
        height: 55px;
        line-height: 55px;
    }

    .van-nav-bar__left, .van-nav-bar__right {
        font-size: 14px;
    }

    .van-nav-bar__title {
        font-size: 18px;
    }

    .van-nav-bar__text {
        color: black;

    }
</style>
