<template>
    <div>
        <div>
            <van-nav-bar
                    title="会员咨询"
                    left-text="返回"
                    left-arrow
                    @click-left="onClickLeft"
                    fixed
                    border
            />
        </div>
        <div style="margin-top: 56px;" v-if="isShow">
            <div style="background-color: white;padding: 10px 0;">
                <van-row>
                    <van-col span="6">
                        <van-image
                                width="60"
                                height="60"
                                :src="userImg"
                        >
                            <template v-slot:error>
                                <van-icon name="user-o"/>
                            </template>
                        </van-image>
                    </van-col>
                    <van-col span="12">
                        <div style="text-align: left;">
                            <div style="height: 40px;">
                                <div style="font-size: 14px;">
                                    {{memberInfo.name}} {{memberInfo.sexN}} {{memberInfo.age}}岁
                                </div>
                                <div style="font-size: 12px;padding-top: 5px;">
                                    {{memberInfo.tel}}
                                </div>
                            </div>

                            <div style="font-size: 12px;">
                                {{memberInfo.createByN}}
                            </div>
                        </div>
                    </van-col>
                    <van-col span="6">
                        <div @click="callPhone">
                            <div style="font-size: 32px;height: 40px;">
                                <van-icon name="phone" color="green"/>
                            </div>
                            <div style="font-size: 12px;">
                                <span>拨打电话</span>
                            </div>
                        </div>
                    </van-col>
                </van-row>
            </div>

            <div v-if="isShow">
                <div style="text-align: left;padding: 10px;background-color: white;">
                    <div>
                        <div>
                            <p><span style="font-size: 16px;font-weight: bold;">第一次提问</span></p>
                        </div>

                        <div>
                            <span style="font-size: 12px;color: #666666;">{{caseList.question}}</span>
                        </div>
                        <div style="padding: 10px 0;">
                            <span style="font-size: 12px;color: #aaaaaa;">{{caseList.questionTimeF}}</span>
                        </div>

                    </div>
                </div>
                <div style="text-align: left;padding: 10px;background-color: white;" v-show="caseList.reply">
                    <div>
                        <div>
                            <p><span style="font-size: 16px;font-weight: bold;">第一次回答</span></p>
                        </div>

                        <div>
                            <span style="font-size: 12px;color: #666666;">{{caseList.reply}}</span>
                        </div>
                        <div style="padding: 10px 0;">
                            <span style="font-size: 12px;color: #aaaaaa;">{{caseList.replyTimeF}}</span>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <div style="background-color: white;position: fixed;bottom: 0;width: 100%;">
            <van-button type="primary" size="large" @click="onSubmit">解答疑问</van-button>

        </div>
    </div>
</template>

<script>
    import headImg from '../assets/timg.jpg'

    export default {
        name: "Advisory",
        data() {
            return {
                isShow: false,
                memberInfo: null,
                caseList: null,
                userImg: null,
                // [
                //     {
                //     id: 1,
                //     name: '委员名称',
                //     questions: '会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字会员提问问题描述文字。',
                //     qTime: '提问时间1',
                //     answers: '委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字委员答疑内容文字。',
                //     aTime: '回答时间1'
                // }
                // ]
            }
        },
        mounted() {
            this.userImg = headImg;
            const that = this;
            const id = that.$route.params.id;
            // eslint-disable-next-line no-console
            console.log(`id.${id}`)
            this.caseList = this.getQuestionById(id);
            // eslint-disable-next-line no-console
            console.log(this.caseList)
            this.memberInfo = this.caseList.memberInfo;
            this.isShow = true;
        },
        methods: {
            getQuestionById(id) {
                const list = this.$store.getters.questionList;
                // eslint-disable-next-line no-console
                console.log(list)
                for (let question of list) {
                    if (question.id === id) {
                        return question;
                    }
                }
            },
            onClickLeft() {
                window.history.length > 1
                    ? this.$router.go(-1)
                    : this.$router.push('/')
                // Toast('返回');
            },
            goMessageBoard() {
                this.$router.push({path: '/messageBoard'})
            },
            goProposals() {
                this.$router.push({path: '/proposals'})
            },
            showToggle() {
                this.isShow = !this.isShow
                if (this.isShow) {
                    this.btnText = "点击隐藏全文"
                } else {
                    this.btnText = "点击查看全文"
                }
            },
            onSubmit() {
                // eslint-disable-next-line no-console
                console.log('@@@@@@@', this.caseList.id)
                this.$router.push({path: '/memberAnswer/' + this.caseList.id})
            },
            callPhone() {
                window.location.href = 'tel://' + this.memberInfo.tel
            }

        },
        computed: {
            getQuestion() {
                // eslint-disable-next-line no-console
                console.log(this.$store.getters.questionList);
                return this.$store.getters.questionList;
            },
            getMemberInfo() {
                return this.caseList.memberInfo;
            }
        }
    }
</script>

<style scoped>
    .van-nav-bar {
        height: 55px;
        line-height: 55px;
    }

    .van-nav-bar__left, .van-nav-bar__right {
        font-size: 16px;
    }

    .van-nav-bar__title {
        font-size: 18px;
    }

    .van-nav-bar .van-icon {
        color: black;
        vertical-align: middle;
        font-size: 18px;
    }

    .van-doc-nav-bar .van-icon {
        color: #969799;
        font-size: 24px;
        cursor: pointer;
    }

    .van-nav-bar__text {
        color: black;

    }
</style>
